"use client";

import type React from "react";
import { useState, useEffect } from "react";

interface Product {
  _id: string;
  name: string;
  sellPrice: number;
  stock: number;
  unit: string;
}

interface OrderItem {
  product: {
    _id: string;
    name: string;
    sellPrice: number;
  };
  quantity: number;
  price: number;
}

interface Order {
  _id: string;
  orderNumber: string;
  customerName: string;
  customerPhone: string;
  items: OrderItem[];
  totalAmount: number;
  status: "pending" | "completed" | "cancelled";
  createdAt: string;
}

const Orders: React.FC = () => {
  const [orders, setOrders] = useState<Order[]>([]);
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(true);
  const [showModal, setShowModal] = useState(false);
  const [showDetailModal, setShowDetailModal] = useState(false);
  const [selectedOrder, setSelectedOrder] = useState<Order | null>(null);
  const [saving, setSaving] = useState(false);
  const [formData, setFormData] = useState({
    customerName: "",
    customerPhone: "",
    items: [{ productId: "", quantity: 1 }],
  });

  useEffect(() => {
    fetchOrders();
    fetchProducts();
  }, []);

  const fetchOrders = async () => {
    try {
      const token = localStorage.getItem("token");
      const response = await fetch("http://localhost:5000/api/orders", {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (response.ok) {
        const data = await response.json();
        setOrders(data);
      }
    } catch (error) {
      console.error("Error fetching orders:", error);
    } finally {
      setLoading(false);
    }
  };

  const fetchProducts = async () => {
    try {
      const token = localStorage.getItem("token");
      const response = await fetch("http://localhost:5000/api/products", {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (response.ok) {
        const data = await response.json();
        setProducts(data);
      }
    } catch (error) {
      console.error("Error fetching products:", error);
    }
  };

  const handleDelete = async (orderId: string) => {
    if (!window.confirm("Bn c chc chn mun xa n hng ny khng?")) {
      return;
    }

    try {
      const token = localStorage.getItem("token");
      const response = await fetch(
        `http://localhost:5000/api/orders/${orderId}`,
        {
          method: "DELETE",
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      if (response.ok) {
        alert("n hng  c xa thnh cng");
        // Refresh orders list
        fetchOrders();
      } else {
        const data = await response.json();
        alert(data.message || "C li xy ra khi xa n hng");
      }
    } catch (error) {
      console.error("Error deleting order:", error);
      alert("C li xy ra khi xa n hng");
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setSaving(true);

    try {
      // Validate form data
      if (!formData.customerName.trim() || !formData.customerPhone.trim()) {
        alert("Vui lng nhp y  thng tin khch hng!");
        setSaving(false);
        return;
      }

      // Validate items
      const validItems = formData.items.filter(
        (item) => item.productId && item.quantity > 0
      );
      if (validItems.length === 0) {
        alert("Vui lng chn t nht mt sn phm!");
        setSaving(false);
        return;
      }

      // Check stock for each item
      for (const item of validItems) {
        const product = products.find((p) => p._id === item.productId);
        if (product && product.stock < item.quantity) {
          alert(
            `Sn phm "${product.name}" khng  s lng. Cn li: ${product.stock}`
          );
          setSaving(false);
          return;
        }
      }

      const token = localStorage.getItem("token");
      const response = await fetch("http://localhost:5000/api/orders", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({
          customerName: formData.customerName.trim(),
          customerPhone: formData.customerPhone.trim(),
          items: validItems,
        }),
      });

      if (response.ok) {
        await fetchOrders();
        await fetchProducts(); // Refresh products to update stock
        setShowModal(false);
        resetForm();
        alert("To n hng thnh cng!");
      } else {
        const error = await response.json();
        alert(error.message || "C li xy ra khi to n hng!");
      }
    } catch (error) {
      console.error("Error creating order:", error);
      alert("C li xy ra khi to n hng!");
    } finally {
      setSaving(false);
    }
  };

  const updateOrderStatus = async (orderId: string, newStatus: string) => {
    try {
      const token = localStorage.getItem("token");
      const response = await fetch(
        `http://localhost:5000/api/orders/${orderId}/status`,
        {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
          body: JSON.stringify({ status: newStatus }),
        }
      );

      if (response.ok) {
        await fetchOrders();
        alert("Cp nht trng thi thnh cng!");
      } else {
        alert("C li xy ra khi cp nht trng thi!");
      }
    } catch (error) {
      console.error("Error updating order status:", error);
      alert("C li xy ra!");
    }
  };

  const resetForm = () => {
    setFormData({
      customerName: "",
      customerPhone: "",
      items: [{ productId: "", quantity: 1 }],
    });
  };

  const addOrderItem = () => {
    setFormData({
      ...formData,
      items: [...formData.items, { productId: "", quantity: 1 }],
    });
  };

  const removeOrderItem = (index: number) => {
    const newItems = formData.items.filter((_, i) => i !== index);
    setFormData({ ...formData, items: newItems });
  };

  const updateOrderItem = (index: number, field: string, value: any) => {
    const newItems = [...formData.items];
    newItems[index] = { ...newItems[index], [field]: value };
    setFormData({ ...formData, items: newItems });
  };

  const getStatusBadge = (status: string) => {
    switch (status) {
      case "pending":
        return (
          <span className="px-3 py-1 text-sm font-semibold rounded-full bg-yellow-100 text-yellow-700">
            Ch x l
          </span>
        );
      case "completed":
        return (
          <span className="px-3 py-1 text-sm font-semibold rounded-full bg-green-100 text-green-700">
            Hon thnh
          </span>
        );
      case "cancelled":
        return (
          <span className="px-3 py-1 text-sm font-semibold rounded-full bg-red-100 text-red-700">
             hy
          </span>
        );
      default:
        return (
          <span className="px-3 py-1 text-sm font-semibold rounded-full bg-blue-100 text-blue-700">
            {status}
          </span>
        );
    }
  };

  const viewOrderDetail = (order: Order) => {
    setSelectedOrder(order);
    setShowDetailModal(true);
  };

  if (loading) {
    return <div className="text-center py-8">ang ti d liu...</div>;
  }

  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-3xl font-bold text-blue-500 mb-6">
          Qun l n hng
        </h1>
        <button
          onClick={() => setShowModal(true)}
          className="inline-flex items-center px-4 py-2 bg-gradient-to-r from-blue-600 to-blue-500 text-white font-semibold rounded-lg shadow-md hover:from-blue-700 hover:to-blue-600 transition-all duration-200 transform hover:scale-105"
        >
          <svg
            className="h-5 w-5 mr-2"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M12 6v6m0 0v6m0-6h6m-6 0H6"
            />
          </svg>
          To n hng
        </button>
      </div>

      {/* Orders Table */}
      <div className="bg-white shadow-lg rounded-lg overflow-hidden border border-gray-200">
        <div className="table-responsive">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gradient-to-r from-blue-50 to-blue-100">
              <tr>
                <th className="px-6 py-4 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  M n hng
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Ngy to
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Khch hng
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  S in thoi
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Tng tin
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Trng thi
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Thao tc
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {orders.map((order) => (
                <tr key={order._id}>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                    {order.orderNumber}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                    {new Date(order.createdAt).toLocaleDateString("vi-VN")}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                    {order.customerName}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                    {order.customerPhone}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                    {order.totalAmount.toLocaleString("vi-VN")} VN
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    {getStatusBadge(order.status)}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium space-x-2">
                    <button
                      onClick={() => viewOrderDetail(order)}
                      className="inline-flex items-center px-3 py-1.5 border border-blue-300 text-blue-600 font-medium rounded-md hover:bg-blue-50 hover:border-blue-400 transition-colors duration-200"
                    >
                      <svg
                        className="h-4 w-4 mr-1"
                        fill="none"
                        stroke="currentColor"
                        viewBox="0 0 24 24"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth={2}
                          d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"
                        />
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth={2}
                          d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z"
                        />
                      </svg>
                      Chi tit
                    </button>
                    {order.status === "pending" && (
                      <>
                        <button
                          onClick={() =>
                            updateOrderStatus(order._id, "completed")
                          }
                          className="inline-flex items-center px-3 py-1.5 border border-green-300 text-green-600 font-medium rounded-md hover:bg-green-50 hover:border-green-400 transition-colors duration-200"
                        >
                          <svg
                            className="h-4 w-4 mr-1"
                            fill="none"
                            stroke="currentColor"
                            viewBox="0 0 24 24"
                          >
                            <path
                              strokeLinecap="round"
                              strokeLinejoin="round"
                              strokeWidth={2}
                              d="M5 13l4 4L19 7"
                            />
                          </svg>
                          Hon thnh
                        </button>
                        <button
                          onClick={() =>
                            updateOrderStatus(order._id, "cancelled")
                          }
                          className="inline-flex items-center px-3 py-1.5 border border-yellow-300 text-yellow-600 font-medium rounded-md hover:bg-yellow-50 hover:border-yellow-400 transition-colors duration-200"
                        >
                          <svg
                            className="h-4 w-4 mr-1"
                            fill="none"
                            stroke="currentColor"
                            viewBox="0 0 24 24"
                          >
                            <path
                              strokeLinecap="round"
                              strokeLinejoin="round"
                              strokeWidth={2}
                              d="M6 18L18 6M6 6l12 12"
                            />
                          </svg>
                          Hy
                        </button>
                      </>
                    )}
                    <button
                      onClick={() => handleDelete(order._id)}
                      className="inline-flex items-center px-3 py-1.5 border border-red-300 text-red-600 font-medium rounded-md hover:bg-red-50 hover:border-red-400 transition-colors duration-200"
                    >
                      <svg
                        className="h-4 w-4 mr-1"
                        fill="none"
                        stroke="currentColor"
                        viewBox="0 0 24 24"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth={2}
                          d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
                        />
                      </svg>
                      Xa
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {/* Create Order Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
          <div className="relative top-10 mx-auto p-5 border w-full max-w-2xl shadow-lg rounded-md bg-white">
            <div className="mt-3">
              <h3 className="text-lg font-medium text-gray-900 mb-4">
                To n hng mi
              </h3>
              <form onSubmit={handleSubmit}>
                <div className="grid grid-cols-2 gap-4 mb-4">
                  <div>
                    <label className="block text-gray-700 text-sm font-bold mb-2">
                      Tn khch hng *
                    </label>
                    <input
                      type="text"
                      value={formData.customerName}
                      onChange={(e) =>
                        setFormData({
                          ...formData,
                          customerName: e.target.value,
                        })
                      }
                      className="form-input"
                      required
                    />
                  </div>
                  <div>
                    <label className="block text-gray-700 text-sm font-bold mb-2">
                      S in thoi *
                    </label>
                    <input
                      type="tel"
                      value={formData.customerPhone}
                      onChange={(e) =>
                        setFormData({
                          ...formData,
                          customerPhone: e.target.value,
                        })
                      }
                      className="form-input"
                      required
                    />
                  </div>
                </div>

                <div className="mb-4">
                  <label className="block text-gray-700 text-sm font-bold mb-2">
                    Sn phm
                  </label>
                  {formData.items.map((item, index) => (
                    <div key={index} className="flex gap-2 mb-2 items-center">
                      <div className="flex-1">
                        <select
                          value={item.productId}
                          onChange={(e) =>
                            updateOrderItem(index, "productId", e.target.value)
                          }
                          className="form-input"
                          required
                        >
                          <option value="">Chn sn phm</option>
                          {products
                            .filter((p) => p.stock > 0)
                            .map((product) => (
                              <option key={product._id} value={product._id}>
                                {product.name} -{" "}
                                {product.sellPrice.toLocaleString("vi-VN")} VN
                                (Cn: {product.stock})
                              </option>
                            ))}
                        </select>
                      </div>
                      <div className="w-24">
                        <input
                          type="number"
                          value={item.quantity}
                          onChange={(e) =>
                            updateOrderItem(
                              index,
                              "quantity",
                              Number(e.target.value)
                            )
                          }
                          className="form-input"
                          min="1"
                          max={
                            products.find((p) => p._id === item.productId)
                              ?.stock || 999
                          }
                          required
                        />
                      </div>
                      <div className="w-32 text-sm text-gray-600">
                        {item.productId && (
                          <span>
                            {(
                              (products.find((p) => p._id === item.productId)
                                ?.sellPrice || 0) * item.quantity
                            ).toLocaleString("vi-VN")}{" "}
                            VN
                          </span>
                        )}
                      </div>
                      {formData.items.length > 1 && (
                        <button
                          type="button"
                          onClick={() => removeOrderItem(index)}
                          className="btn-danger px-2 py-1 text-sm"
                        >
                          Xa
                        </button>
                      )}
                    </div>
                  ))}
                  <button
                    type="button"
                    onClick={addOrderItem}
                    className="btn-secondary text-sm mt-2"
                  >
                    + Thm sn phm
                  </button>

                  {/* Tng tin */}
                  <div className="mt-4 p-3 bg-gray-50 rounded-lg">
                    <div className="flex justify-between items-center">
                      <span className="font-medium">Tng tin:</span>
                      <span className="text-lg font-bold text-blue-600">
                        {formData.items
                          .reduce((total, item) => {
                            const product = products.find(
                              (p) => p._id === item.productId
                            );
                            return (
                              total +
                              (product ? product.sellPrice * item.quantity : 0)
                            );
                          }, 0)
                          .toLocaleString("vi-VN")}{" "}
                        VN
                      </span>
                    </div>
                  </div>
                </div>

                <div className="flex justify-end space-x-3">
                  <button
                    type="button"
                    onClick={() => setShowModal(false)}
                    className="btn-secondary"
                    disabled={saving}
                  >
                    Hy
                  </button>
                  <button
                    type="submit"
                    className="btn-primary disabled:opacity-50"
                    disabled={saving}
                  >
                    {saving ? "ang to..." : "To n hng"}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}

      {/* Order Detail Modal */}
      {showDetailModal && selectedOrder && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
          <div className="relative top-10 mx-auto p-5 border w-full max-w-2xl shadow-lg rounded-md bg-white">
            <div className="mt-3">
              <div className="flex justify-between items-center mb-4">
                <h3 className="text-lg font-medium text-gray-900">
                  Chi tit n hng
                </h3>
                <button
                  onClick={() => setShowDetailModal(false)}
                  className="text-gray-400 hover:text-gray-600"
                >
                  
                </button>
              </div>

              <div className="grid grid-cols-2 gap-4 mb-6">
                <div>
                  <p>
                    <strong>M n hng:</strong> {selectedOrder.orderNumber}
                  </p>
                  <p>
                    <strong>Khch hng:</strong> {selectedOrder.customerName}
                  </p>
                  <p>
                    <strong>S in thoi:</strong>{" "}
                    {selectedOrder.customerPhone}
                  </p>
                </div>
                <div>
                  <p>
                    <strong>Ngy to:</strong>{" "}
                    {new Date(selectedOrder.createdAt).toLocaleString("vi-VN")}
                  </p>
                  <p>
                    <strong>Trng thi:</strong>{" "}
                    {getStatusBadge(selectedOrder.status)}
                  </p>
                  <p>
                    <strong>Tng tin:</strong>{" "}
                    {selectedOrder.totalAmount.toLocaleString("vi-VN")} VN
                  </p>
                </div>
              </div>

              <div className="mb-4">
                <h4 className="font-medium mb-2">Danh sch sn phm:</h4>
                <table className="min-w-full divide-y divide-gray-200">
                  <thead className="bg-gray-50">
                    <tr>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">
                        Sn phm
                      </th>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">
                        S lng
                      </th>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">
                        n gi
                      </th>
                      <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">
                        Thnh tin
                      </th>
                    </tr>
                  </thead>
                  <tbody className="bg-white divide-y divide-gray-200">
                    {selectedOrder.items.map((item, index) => (
                      <tr key={index}>
                        <td className="px-4 py-2 text-sm text-gray-900">
                          {item.product.name}
                        </td>
                        <td className="px-4 py-2 text-sm text-gray-900">
                          {item.quantity}
                        </td>
                        <td className="px-4 py-2 text-sm text-gray-900">
                          {item.price.toLocaleString("vi-VN")} VN
                        </td>
                        <td className="px-4 py-2 text-sm text-gray-900">
                          {(item.quantity * item.price).toLocaleString("vi-VN")}{" "}
                          VN
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Orders;
